@import url(https://fonts.googleapis.com/css?family=Arizonia%7CJosefin+Sans%7CMontserrat&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  text-align: center;
  color: pink; }
  @media (min-width: 501px) {
    .App {
      color: green; } }
  @media (min-width: 801px) {
    .App {
      color: black; } }

.App-logo {
  height: 40vmin; }

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white; }

.App-link {
  color: #09d3ac; }

.topBanor {
  height: 100px;
  background: #958adb; }

.navBar {
  display: flex;
  flex-flow: column wrap;
  margin: auto 4px;
  background: #958adb;
  position: absolute;
  top: 0px;
  z-index: 999;
  width: 100%; }
  .navBar a {
    width: 90%;
    border: solid 2px black;
    margin: 1px auto;
    padding: 20px;
    font-size: 2rem;
    background-color: #b2c3e7;
    color: black;
    text-decoration: none; }

